
import React, { useState } from 'react'
import { Link } from "react-router-dom";

import BtnAction from './btn-action'


import './login-form.scss';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';
import BootstrapInput from './bootstrapInput'



const LoginForm = () => {
  
  const [user, setUser] = useState({ dni: '55', username: 't' });
  const handleInput = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value
    });

  }
  

  return (
    <section className="aside__rigth">
      <h2 className="intro__title">Protégelos <span className="intro__title--span">ahora</span> </h2>
      <h2 className="intro__title">Ingresa los datos del titular.</h2>
      <form autoComplete="off">
        <div className="input__dni">
          <FormControl>
            <NativeSelect
              input={<BootstrapInput name="age" id="age-customized-native-simple" />}>
              <option> DNI </option>
              <option>CE</option>
              <option>RUC</option>
            </NativeSelect>
          </FormControl>

          <input className="input" type="text" name="dni" placeholder="Nro. de Documento" onChange={(e) => handleInput(e)} required />
        </div>
        <input className="input" type="text" name="username" placeholder="Nombre" onChange={(e) => handleInput(e)} />
        <div className="intro__policies">

          <input type="checkbox" id="cbox2" value="second_checkbox" />
          <label for="cbox2">Acepto la <u>Política de Protección de Datos Personales</u> y los <u>Términos y Condiciones.</u></label>
        </div>
        <div className="intro__policies">
          <input type="checkbox" id="cbox2" value="second_checkbox" />
          <label for="cbox2">Acepto la Política de Envío de <u>Comunicaciones Comerciales.</u></label>
        </div>
        <Link to={{ pathname: '/pasouno', state: user }}> <BtnAction title={'COMENCEMOS'} /> </Link>
      </form>
    </section>
  )
}

export default LoginForm;