import React, { useState } from 'react'


const AddUserForm = (props) => {
const [user, setUser] = useState({username:'', dni:'', datebirth:''})

const handleInput = (e) => {
  setUser({
    ...user,
    [e.target.name]: e.target.value
  });

}

  return (
    <div>
      <div>
        <h4>Asegurados (0{props.user.length})</h4>
        <a>VER TODOS</a>
        <h3>Datos del asegurado Nº{props.user.length+1}</h3>
        <form>
          <input className="input" type="text" name="dni" placeholder="Nro. de Documento"  onChange={(e) => handleInput(e)}/>
          <input className="input" type="text" name="username" placeholder="Nombres" onChange={(e) => handleInput(e)}/>
          <input className="input" type="text" name="dni" placeholder="Apellido Materno" />
          <input className="input" type="text" name="dni" placeholder="Apellido Materno" />
          <input className="input" type="date" name="datebirth" placeholder="Fecha de nacimiento" onChange={(e) => handleInput(e)}/>
          <h4>Genero</h4>
          <input type="radio" name="dni" placeholder="" /> <label>Masculino</label>
          <input type="radio" name="dni" placeholder="" /> <label>Femenino</label>

          <a> Cancelar</a>
          <button className="btn-actived" type="button"  onClick={() => { props.user.push(user);  props.hidden(false); props.show(true) }}>GUARDAR EDICIÓN</button>  
        </form>
      </div>
    </div>
  )
}
export default AddUserForm;