import React from 'react';

 import { Switch, BrowserRouter, Route } from "react-router-dom";
/* import logo from './logo.svg'; */


import Intro from '../src/ view/intro';
import PasoUno from './ view/pasoUno';
import PasoDos from './ view/pasoDos';
import PasoTres from './ view/pasoTres';    

const App = () => {
  return (
    <BrowserRouter>
     <Switch>
      <Route exact path="/" exact component={Intro} />
      <Route exact path="/pasouno" exact component={PasoUno} />
      <Route exact path="/pasodos" exact component={PasoDos} />
      <Route exact path="/pasotres" exact component={PasoTres} />
      {/* <Route path="/pasouno/" exact component={PasoUno} />
      <Route path="/pasodos/" exact component={PasoDos} />
      <Route path="/pasotres/" exact component={PasoTres} /> */}
      </Switch>
      </BrowserRouter>
  ); 
}

export default App;