import React, { useState } from 'react'
import { Link } from "react-router-dom";

import './user-info-form.scss';
import FormControl from '@material-ui/core/FormControl';
import NativeSelect from '@material-ui/core/NativeSelect';

import BtnAction from './btn-action'
import BootstrapInput from './bootstrapInput'

const UserInfoForm = (props) => {
  const [user, setUser] = useState({ dni: props.user.dni, username: props.user.username, email: '', datebirth: '02/03/2000'});
  const handleInput = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value
    });
  }
  return (
    <section className="section__user">

      <h2 className="welcome__text">Hola, <span>{props.user.username} </span> </h2>
      <h2 className="welcome__text">cuéntanos sobre ti</h2>
      <h4 className="subtitle"> Podrás protegerlos  <span> por soloS/20 al mes </span> por asegurado</h4>
      <h4 className="subtitle"> Datos del titular </h4>

      <form>
        <div className="input__dni">
        <FormControl>
          <NativeSelect
            input={<BootstrapInput name="age" id="age-customized-native-simple" />}>
            <option> DNI </option>
            <option>CE</option>
            <option>RUC</option>

          </NativeSelect>
        </FormControl>
          <input type="text" className="input" name="dni" placeholder="Nro. Documento" value={props.user.dni} /> 
        </div>

        <input type="text"className="input"  name="email" placeholder="Correo" onChange={(e) => handleInput(e)} />
        <h4>¿ESTE SEGURO ES PARA TI? </h4>

        <input type="radio" id="cbox2" value="second_checkbox" />
        <label for="cbox2"> Si </label>
        <input type="radio" id="cbox2" value="second_checkbox" />
        <label for="cbox2">No</label>
        <div className="btn__next">
        <p>Modificar DNI</p>
        <Link to={{ pathname: '/pasodos', state: user }}>
          <BtnAction type="button" title="CONTINUAR" />
        </Link>
        </div>
      </form>
    </section>
  )
}
export default UserInfoForm