import React, { useState } from 'react'
import ItemUser from'../user-item'



const TotalPayment = (props) => {

  const [showUsers, setUser] = useState(false)
  const [showDetails, setDetails] = useState(true)
 

  return (
    <div>
      <h5>PAGO TOTAL</h5>
      <div><h6>Por {props.user.length} asegurados</h6>
      { showDetails &&  <h6 onClick={() => {setUser(!showUsers); setDetails(!showDetails) }}>DETALLE</h6>}
      { showUsers && <h6 onClick={() => {setDetails(!showDetails); setUser(!showUsers)}}>OCULTAR</h6>}
      { showUsers && <ItemUser user={props.user}show={false}/>}
      </div>
      s/{props.total}
      <style jsx>{`
         
    `}</style>
    </div>

  )
}
export default TotalPayment;