import React, {  useState } from 'react'
import {Link} from "react-router-dom";

import './users.scss';
import BtnAction from './btn-action'
import UserItem from './user-item'

import AddUserForm from './add-user-form';


const Users = (props) => {
 
  const [users] = useState([props.user])
  const [showUsers, setShow] = useState(true)
  const [showForm, setForm] = useState(false)

  return (
    <div className="section__user">
     
        <h2 className="welcome__text">Tus  <span>asegurados </span></h2>
        <h3 className="subtitle"> Preséntanos a quién vamos a proteger. </h3>
      <hr/>
      {showUsers && <UserItem user={users} show={true}/>}
      {showUsers &&
        <div className="btn__setcion">
          <span className="">+</span>
          <button className ="btn__add" onClick={() => { setForm(!showForm); setShow(!showUsers) }}>Quiero asegurar a alguien más</button> 
        </div>
      }
      {showForm && <AddUserForm user={users} show={setShow} hidden={setForm} />}

      {showUsers &&  <Link to= {{ pathname: '/pasotres', state: users }} ><BtnAction title={'CONTINUAR >'} /> </Link>} 

    </div>

  )
}


export default Users