import React, { useState } from 'react'




const DiscountCode = (props) => {
  const [input, setInput] = useState(true)
  const [message, setmessage] = useState(false)
   
  const showDiscountMessage = () => {
    props.total(32)
    setInput(false)
    setmessage(true)
  }
  return (
    <div>
      {input && <div> <input type="text" name="dni" placeholder="Ingresar código de dscto. " /> 
        <span> <button onClick={() => showDiscountMessage()}> </button></span> Aplicar </div>}
      {message && <div> <h4> ¡Genial! tienes 20% dscto extra. </h4></div>}
    </div>

  )
}


export default DiscountCode