import React, { useState } from 'react'

import './user-item.scss';

const UserItem = (props) => {

  let users = props.user;

  let [user, setUser] = useState(users)
  const deleteUser = (i) => {
    return props.user.splice(i, 1)
  }
  return (user.map((ele, i) => {
    return (
      <div>
      
        <h2 className="username__text"> {ele.username} </h2>
        <div className = "data__date">
          <div>
        <h2 className="delete__desktop"> DNI {ele.dni}  -F.N {ele.datebirth}</h2>
        </div>
        {props.show && <h2 className="delete__desktop" >Editar</h2>}
        {props.show && <h2 className="delete__desktop" onClick={() => { setUser(deleteUser(ele)) }}>Eliminar</h2>}
        </div>
        <hr />
      </div>

    )
  }))
}
export default UserItem;